<template>
  <header class="header">
    <div class="header__inner">
      <div
        class="header__brand"
        style="background: #fff; border-bottom: 1px solid #141543"
      >
        <div class="brand-wrap">
          <a href="javascript:void(0)" class="brand-img stretched-link">
            <img
              :src="`${publicPath}img/logo-min.png`"
              alt="TAME Logo"
              class="logo"
              width="40"
              height="40"
            />
          </a>
          <div class="brand-title">TAME</div>
        </div>
      </div>
      <div class="header__content">
        <div class="header__content-start"></div>
        <div class="header__content-end">
          <div class="d-flex align-items-center">
            <div
              class="d-flex align-items-center cursor-pointer"
              :class="{ 'text-danger': invalidDate }"
              @click="goToOrders()"
            >
              <span
                class="h4 mb-0"
                style="color: #fff"
                :class="{ 'text-danger': invalidDate }"
              >
                {{ systemDate }}
              </span>
              <div class="mx-2">
                <digital-clock />
              </div>
            </div>
          </div>
          <div class="ms-2">
            <button
              class="header__btn btn btn-icon btn-sm"
              type="button"
              data-bs-toggle="dropdown"
              aria-label="User dropdown"
              aria-expanded="false"
            >
              <i class="demo-psi-male"></i>
            </button>

            <div
              class="dropdown-menu dropdown-menu-end w-md-200px"
              data-popper-placement="bottom-end"
              style=""
            >
              <div class="d-flex align-items-center border-bottom px-3 py-2">
                <div class="flex-shrink-0">
                  <avatar
                    :name="loggedUser.name"
                    width="2.25rem"
                    height="2.25rem"
                  ></avatar>
                </div>
                <div class="flex-grow-1 ms-3">
                  <h5 class="mb-0">{{ loggedUser.name }}</h5>
                  <span class="text-muted fst-italic">{{
                    userRole || "Administrator"
                  }}</span>
                </div>
              </div>

              <div class="row">
                <div class="col-md-12">
                  <div class="list-group list-group-borderless h-100 py-3">
                    <a
                      href="javascript:void(0)"
                      @click.prevent="signout"
                      class="list-group-item list-group-item-action"
                    >
                      <i class="demo-pli-unlock fs-5 me-2"></i> Logout
                    </a>
                  </div>
                </div>
              </div>
            </div>
          </div>
        </div>
      </div>
    </div>
  </header>
</template>
<script>
import Avatar from "@/components/Avatar.vue";
import DigitalClock from "@/components/DigitalClock.vue";
import { permissionsHandler } from "@/common/permissions-handler";
export default {
  name: "AppHeader",
  mixins: [permissionsHandler],
  components: {
    Avatar,
    DigitalClock,
  },
  computed: {
    systemDate() {
      return this.$store.state.system_date;
    },
    invalidDate() {
      return this.$store.state.invalid_date;
    },
  },
  methods: {
    goToOrders() {
      if (!this.invalidDate || !this.isCashier) {
        return;
      }
      this.$router.push({ name: "EOD_Handler" });
    },
    signout() {
      this.$http
        .post("auth/logout", this.$helper.generateFormData({}))
        .then((response) => {
          if (response.data.status) {
            this.$store.dispatch("logout").then(() => {
              window.location.replace(
                this.$router.resolve({ name: "FrontOfficeLogin" }).href
              );
            });
          }
        });
    },
  },
};
</script>
